.table {
  font-family: $font-family-os!important;
  border-collapse: collapse;
  border-spacing: 0;
  border: none!important;
  width: 100%;

  & tbody tr:nth-child(even) {
    background-color: #F5F5F6;
  }

  & td {
    padding: 8px;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .table {
    display: block;
    width: 100%;
    overflow-x: auto;
  }

}
